  -webkit-user-select: none;
  touch-action: manipulation;
}

.buttonD9:not(:disabled):hover {
  -webkit-transform: scale(1.05);
  transform: scale(1.05);
}

.buttonD9:not(:disabled):hover:active {
  -webkit-transform: scale(1.05) translateY(.125rem);
  transform: scale(1.05) translateY(.125rem);
}

.buttonD9:focus {
  outline: 0 solid transparent;
}

.buttonD9:focus:before {
  content: "";
  left: calc(-1*.375rem);
  pointer-events: none;
  position: absolute;
  top: calc(-1*.375rem);
  transition: border-radius;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.buttonD9:focus:not(:focus-visible) {
