.nav-menu {
    background-color: #060b26;
    width: 250px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    left: -100%;
    transition: 850ms;
    opacity:1;
 
    overflow-x  : hidden;
    backface-visibility: hidden;
    cursor:pointer;

   
  }
  .nav-menu.active {
    left: 0;
    transition: 350ms;
    overflow-x  : hidden;
    backface-visibility: hidden;
    cursor:pointer;
    display:inline-block;
    /* z-index: 1; */
   
  }

.nav-text{
    display: flex;
    justify-content: start;
    align-items:center ;
    padding: 5px 8px 8px 0px;
}

.nav-text a {
    text-decoration: none;
    color: white;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: inline-block;
    align-items: center;
    padding: 5px 0px;
    border-radius: 4px;
}

.nav-text a:hover{
background-color: blue;
color: aliceblue;
}

.nav-menu-items{
    width: 100%;
}

